@import url(../node_modules/purecss/build/grids.css);
@import url(../node_modules/purecss/build/grids-responsive.css);
.sidebar {
  background-color: #3d4f5d;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 1em; }

.sidebar h2 {
  color: #e4f1fb;
  margin-top: 0; }

.sidebar .form-group {
  margin-bottom: 1em; }

.sidebar .form-group input {
  background-color: #546674;
  border: 1px solid #3d4f5d;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #e4f1fb;
  display: block;
  font-size: 1em;
  padding: 0.8em;
  width: 100%; }

.sidebar .form-group label {
  color: #b0cadb;
  display: inline-block;
  font-size: 0.9em;
  margin-bottom: 0.2em; }

.sidebar .form-group select {
  -webkit-appearance: none;
  background-color: #546674;
  border: 1px solid #3d4f5d;
  border-radius: 0;
  color: #e4f1fb;
  display: block;
  font-size: 1em;
  padding: 0.8em;
  width: 100%; }

@media screen and (min-width: 48em) {
  .sidebar {
    bottom: 0;
    position: fixed;
    top: 0; } }

.content {
  padding: 1em; }

.content h2 {
  margin-top: 0; }

@media screen and (min-width: 48em) {
  .content {
    margin-left: 20%; } }



